/* Basic Defaults */

body {
  background: black;
  color: rgb(229, 177, 58);
  margin: 0;
  padding: 0;
  line-height: 16px;
	letter-spacing: 0em;
  font-weight: 300;
}


/* Temporary border */
table, th, td,
.border {
  border: 1px solid rgb(229, 177, 58);

}
/* Text */
@font-face {
  font-family: Star Jedi;
  font-style: normal;
  font-weight: 400;
  src: url(/starwars-checklist/static/media/Starjedi.cc3019aa.ttf) format('truetype')
}

.jedi-font {
  font-family: Star Jedi, serif;
  color: rgb(229, 177, 58);
}

.gothic-font {
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif
}

.is-checked {
  color: rgb(130, 99, 31);
}

/* Formatting */
.margin-lr-auto {
  margin-left:auto;
  margin-right:auto;
}

.center-text {
  text-align: center;
}
